.roll-in-out {
  position: relative;
  color: black;
  text-decoration: none;
}

.roll-in-out::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 100%;
  background: currentColor;
  transform: scaleX(0);
  -webkit-transform: scaleX(0);
  -moz-transform: scaleX(0);
  -ms-transform: scaleX(0);
  -o-transform: scaleX(0);
  -webkit-transform-origin: right;
          transform-origin: right;
  -webkit-transition: -webkit-transform 250ms ease-in;
  transition: -webkit-transform 250ms ease-in;
  transition: transform 250ms ease-in;
  transition: transform 250ms ease-in, -webkit-transform 250ms ease-in;
  -webkit-transition: transform 250ms ease-in;
  -moz-transition: transform 250ms ease-in;
  -ms-transition: transform 250ms ease-in;
  -o-transition: transform 250ms ease-in;
}

.roll-in-out:hover::after {
  transform: scaleX(1);
  -webkit-transform: scaleX(1);
  -moz-transform: scaleX(1);
  -ms-transform: scaleX(1);
  -o-transform: scaleX(1);
  -webkit-transform-origin: left;
          transform-origin: left;
}

.signature:nth-child(1) {
  stroke-dasharray: 3px;
}

.signature:nth-child(2) {}

.signature:nth-child(3) {}

.signature:nth-child(4) {}
body {
  margin: 0;
  padding: 0;
  font-size: 100%;
  line-height: 1.6;
}

#wrapper {
  position: relative;
  width: 100%;
  min-height: 51.2vw;
  overflow: hidden;
}

.layer {
  position: absolute;
  width: 100vw;
  min-height: 51.2vw;
  overflow: hidden;
}

.layer .content-wrap {
  position: absolute;
  width: 100vw;
  min-height: 51.2vw;
}

.layer .content-body {
  width: 25%;
  position: absolute;
  top: 50%;
  text-align: center;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.layer img {
  position: absolute;
  height: 60%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.layer h1 {
  font-size: 2em;
}

.bottom {
  background: var(--primary_color);
  z-index: 1;
}

.bottom .content-body {
  right: 5%;
}

.top {
  background: var(--accent_color);
  z-index: 2;
  width: 50vw;
}

.top .content-body {
  left: 5%;
}

.content-body {
  z-index: 3;
}

.handle {
  position: absolute;
  height: 100%;
  display: block;
  background: var(--secondary_color);
  width: 0px;
  top: 0;
  left: 50%;
  z-index: 3;
}

.skewed .handle {
  top: 49%;
  -webkit-transform: rotate(25deg) translateY(-50%);
          transform: rotate(25deg) translateY(-50%);
  height: 200%;
  -webkit-transform-origin: top;
          transform-origin: top;
}

.skewed .top {
  -webkit-transform: skew(-25deg);
          transform: skew(-25deg);
  margin-left: -1000px;
  width: calc(50vw + 1000px);
}

.skewed .top .content-wrap {
  -webkit-transform: skew(25deg);
          transform: skew(25deg);
  margin-left: 1000px;
}

.logo {
  position: absolute;
  z-index: 4;
  bottom: 5%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 15%
}

.logo path {
  fill: var(--secondary_color) !important;
}

.logo text {
  fill: var(--secondary_color) !important;
}


